var utils = require('../utils');

var trigger = {
  renameColumnTrigger: function renameColumnTrigger(logger, tableName, columnName, to) {
    var triggerName = utils.generateCombinedName(logger, 'autoinc_trg', tableName);
    var sequenceName = utils.generateCombinedName(logger, 'seq', tableName);
    return "DECLARE " + "PK_NAME VARCHAR(200); " + "IS_AUTOINC NUMBER := 0; " + "BEGIN" + "  EXECUTE IMMEDIATE ('ALTER TABLE \"".concat(tableName, "\" RENAME COLUMN \"").concat(columnName, "\" TO \"").concat(to, "\"');") + "  SELECT COUNT(*) INTO IS_AUTOINC from \"USER_TRIGGERS\" where trigger_name = '".concat(triggerName, "';") + "  IF (IS_AUTOINC > 0) THEN" + "    SELECT cols.column_name INTO PK_NAME" + "    FROM all_constraints cons, all_cons_columns cols" + "    WHERE cons.constraint_type = 'P'" + "    AND cons.constraint_name = cols.constraint_name" + "    AND cons.owner = cols.owner" + "    AND cols.table_name = '".concat(tableName, "';") + "    IF ('".concat(to, "' = PK_NAME) THEN") + "      EXECUTE IMMEDIATE ('DROP TRIGGER \"".concat(triggerName, "\"');") + "      EXECUTE IMMEDIATE ('create or replace trigger \"".concat(triggerName, "\"") + "      BEFORE INSERT on \"".concat(tableName, "\" for each row") + "        declare" + "        checking number := 1;" + "        begin" + "          if (:new.\"".concat(to, "\" is null) then") + "            while checking >= 1 loop" + "              select \"".concat(sequenceName, "\".nextval into :new.\"").concat(to, "\" from dual;") + "              select count(\"".concat(to, "\") into checking from \"").concat(tableName, "\"") + "              where \"".concat(to, "\" = :new.\"").concat(to, "\";") + "            end loop;" + "          end if;" + "        end;');" + "    end if;" + "  end if;" + "END;";
  },
  createAutoIncrementTrigger: function createAutoIncrementTrigger(logger, tableName) {
    var triggerName = utils.generateCombinedName(logger, 'autoinc_trg', tableName);
    var sequenceName = utils.generateCombinedName(logger, 'seq', tableName);
    return "DECLARE " + "PK_NAME VARCHAR(200); " + "BEGIN" + "  EXECUTE IMMEDIATE ('CREATE SEQUENCE \"".concat(sequenceName, "\"');") + "  SELECT cols.column_name INTO PK_NAME" + "  FROM all_constraints cons, all_cons_columns cols" + "  WHERE cons.constraint_type = 'P'" + "  AND cons.constraint_name = cols.constraint_name" + "  AND cons.owner = cols.owner" + "  AND cols.table_name = '".concat(tableName, "';") + "  execute immediate ('create or replace trigger \"".concat(triggerName, "\"") + "  BEFORE INSERT on \"".concat(tableName, "\"") + "  for each row" + "  declare" + "  checking number := 1;" + "  begin" + "    if (:new.\"' || PK_NAME || '\" is null) then" + "      while checking >= 1 loop" + "        select \"".concat(sequenceName, "\".nextval into :new.\"' || PK_NAME || '\" from dual;") + "        select count(\"' || PK_NAME || '\") into checking from \"".concat(tableName, "\"") + "        where \"' || PK_NAME || '\" = :new.\"' || PK_NAME || '\";" + "      end loop;" + "    end if;" + "  end;'); " + "END;";
  },
  renameTableAndAutoIncrementTrigger: function renameTableAndAutoIncrementTrigger(logger, tableName, to) {
    var triggerName = utils.generateCombinedName(logger, 'autoinc_trg', tableName);
    var sequenceName = utils.generateCombinedName(logger, 'seq', tableName);
    var toTriggerName = utils.generateCombinedName(logger, 'autoinc_trg', to);
    var toSequenceName = utils.generateCombinedName(logger, 'seq', to);
    return "DECLARE " + "PK_NAME VARCHAR(200); " + "IS_AUTOINC NUMBER := 0; " + "BEGIN" + "  EXECUTE IMMEDIATE ('RENAME \"".concat(tableName, "\" TO \"").concat(to, "\"');") + "  SELECT COUNT(*) INTO IS_AUTOINC from \"USER_TRIGGERS\" where trigger_name = '".concat(triggerName, "';") + "  IF (IS_AUTOINC > 0) THEN" + "    EXECUTE IMMEDIATE ('DROP TRIGGER \"".concat(triggerName, "\"');") + "    EXECUTE IMMEDIATE ('RENAME \"".concat(sequenceName, "\" TO \"").concat(toSequenceName, "\"');") + "    SELECT cols.column_name INTO PK_NAME" + "    FROM all_constraints cons, all_cons_columns cols" + "    WHERE cons.constraint_type = 'P'" + "    AND cons.constraint_name = cols.constraint_name" + "    AND cons.owner = cols.owner" + "    AND cols.table_name = '".concat(to, "';") + "    EXECUTE IMMEDIATE ('create or replace trigger \"".concat(toTriggerName, "\"") + "    BEFORE INSERT on \"".concat(to, "\" for each row") + "      declare" + "      checking number := 1;" + "      begin" + "        if (:new.\"' || PK_NAME || '\" is null) then" + "          while checking >= 1 loop" + "            select \"".concat(toSequenceName, "\".nextval into :new.\"' || PK_NAME || '\" from dual;") + "            select count(\"' || PK_NAME || '\") into checking from \"".concat(to, "\"") + "            where \"' || PK_NAME || '\" = :new.\"' || PK_NAME || '\";" + "          end loop;" + "        end if;" + "      end;');" + "  end if;" + "END;";
  }
};
module.exports = trigger;