// PostgreSQL Query Builder & Compiler
// ------
var inherits = require('inherits');

var QueryCompiler = require('../../../query/compiler');

var _require = require('lodash'),
    reduce = _require.reduce,
    identity = _require.identity;

function QueryCompiler_PG(client, builder) {
  QueryCompiler.call(this, client, builder);
}

inherits(QueryCompiler_PG, QueryCompiler);
Object.assign(QueryCompiler_PG.prototype, {
  // Compiles a truncate query.
  truncate: function truncate() {
    return "truncate ".concat(this.tableName, " restart identity");
  },
  // is used if the an array with multiple empty values supplied
  _defaultInsertValue: 'default',
  // Compiles an `insert` query, allowing for multiple
  // inserts using a single query statement.
  insert: function insert() {
    var sql = QueryCompiler.prototype.insert.call(this);
    if (sql === '') return sql;
    var returning = this.single.returning;
    return {
      sql: sql + this._returning(returning),
      returning: returning
    };
  },
  // Compiles an `update` query, allowing for a return value.
  update: function update() {
    var withSQL = this.with();

    var updateData = this._prepUpdate(this.single.update);

    var wheres = this.where();
    var returning = this.single.returning;
    return {
      sql: withSQL + "update ".concat(this.single.only ? 'only ' : '').concat(this.tableName, " ") + "set ".concat(updateData.join(', ')) + (wheres ? " ".concat(wheres) : '') + this._returning(returning),
      returning: returning
    };
  },
  // Compiles an `update` query, allowing for a return value.
  del: function del() {
    var sql = QueryCompiler.prototype.del.apply(this, arguments);
    var returning = this.single.returning;
    return {
      sql: sql + this._returning(returning),
      returning: returning
    };
  },
  aggregate: function aggregate(stmt) {
    return this._aggregate(stmt, {
      distinctParentheses: true
    });
  },
  _returning: function _returning(value) {
    return value ? " returning ".concat(this.formatter.columnize(value)) : '';
  },
  // Join array of table names and apply default schema.
  _tableNames: function _tableNames(tables) {
    var schemaName = this.single.schema;
    var sql = [];

    for (var i = 0; i < tables.length; i++) {
      var tableName = tables[i];

      if (tableName) {
        if (schemaName) {
          tableName = "".concat(schemaName, ".").concat(tableName);
        }

        sql.push(this.formatter.wrap(tableName));
      }
    }

    return sql.join(', ');
  },
  forUpdate: function forUpdate() {
    var tables = this.single.lockTables || [];
    return 'for update' + (tables.length ? ' of ' + this._tableNames(tables) : '');
  },
  forShare: function forShare() {
    var tables = this.single.lockTables || [];
    return 'for share' + (tables.length ? ' of ' + this._tableNames(tables) : '');
  },
  skipLocked: function skipLocked() {
    return 'skip locked';
  },
  noWait: function noWait() {
    return 'nowait';
  },
  // Compiles a columnInfo query
  columnInfo: function columnInfo() {
    var column = this.single.columnInfo;
    var schema = this.single.schema; // The user may have specified a custom wrapIdentifier function in the config. We
    // need to run the identifiers through that function, but not format them as
    // identifiers otherwise.

    var table = this.client.customWrapIdentifier(this.single.table, identity);

    if (schema) {
      schema = this.client.customWrapIdentifier(schema, identity);
    }

    var sql = 'select * from information_schema.columns where table_name = ? and table_catalog = ?';
    var bindings = [table, this.client.database()];

    if (schema) {
      sql += ' and table_schema = ?';
      bindings.push(schema);
    } else {
      sql += ' and table_schema = current_schema()';
    }

    return {
      sql: sql,
      bindings: bindings,
      output: function output(resp) {
        var out = reduce(resp.rows, function (columns, val) {
          columns[val.column_name] = {
            type: val.data_type,
            maxLength: val.character_maximum_length,
            nullable: val.is_nullable === 'YES',
            defaultValue: val.column_default
          };
          return columns;
        }, {});
        return column && out[column] || out;
      }
    };
  }
});
module.exports = QueryCompiler_PG;