// MySQL Column Compiler
// -------
var inherits = require('inherits');

var ColumnCompiler = require('../../../schema/columncompiler');

var _require = require('lodash'),
    isObject = _require.isObject;

function ColumnCompiler_MySQL() {
  ColumnCompiler.apply(this, arguments);
  this.modifiers = ['unsigned', 'nullable', 'defaultTo', 'comment', 'collate', 'first', 'after'];
}

inherits(ColumnCompiler_MySQL, ColumnCompiler); // Types
// ------

Object.assign(ColumnCompiler_MySQL.prototype, {
  increments: 'int unsigned not null auto_increment primary key',
  bigincrements: 'bigint unsigned not null auto_increment primary key',
  bigint: 'bigint',
  double: function double(precision, scale) {
    if (!precision) return 'double';
    return "double(".concat(this._num(precision, 8), ", ").concat(this._num(scale, 2), ")");
  },
  integer: function integer(length) {
    length = length ? "(".concat(this._num(length, 11), ")") : '';
    return "int".concat(length);
  },
  mediumint: 'mediumint',
  smallint: 'smallint',
  tinyint: function tinyint(length) {
    length = length ? "(".concat(this._num(length, 1), ")") : '';
    return "tinyint".concat(length);
  },
  text: function text(column) {
    switch (column) {
      case 'medium':
      case 'mediumtext':
        return 'mediumtext';

      case 'long':
      case 'longtext':
        return 'longtext';

      default:
        return 'text';
    }
  },
  mediumtext: function mediumtext() {
    return this.text('medium');
  },
  longtext: function longtext() {
    return this.text('long');
  },
  enu: function enu(allowed) {
    return "enum('".concat(allowed.join("', '"), "')");
  },
  datetime: function datetime(precision) {
    if (isObject(precision)) {
      var _precision = precision;
      precision = _precision.precision;
    }

    return typeof precision === 'number' ? "datetime(".concat(precision, ")") : 'datetime';
  },
  timestamp: function timestamp(precision) {
    if (isObject(precision)) {
      var _precision2 = precision;
      precision = _precision2.precision;
    }

    return typeof precision === 'number' ? "timestamp(".concat(precision, ")") : 'timestamp';
  },
  time: function time(precision) {
    if (isObject(precision)) {
      var _precision3 = precision;
      precision = _precision3.precision;
    }

    return typeof precision === 'number' ? "time(".concat(precision, ")") : 'time';
  },
  bit: function bit(length) {
    return length ? "bit(".concat(this._num(length), ")") : 'bit';
  },
  binary: function binary(length) {
    return length ? "varbinary(".concat(this._num(length), ")") : 'blob';
  },
  json: function json() {
    return 'json';
  },
  // Modifiers
  // ------
  defaultTo: function defaultTo(value) {
    // MySQL defaults to null by default, but breaks down if you pass it explicitly
    // Note that in MySQL versions up to 5.7, logic related to updating
    // timestamps when no explicit value is passed is quite insane - https://dev.mysql.com/doc/refman/5.7/en/server-system-variables.html#sysvar_explicit_defaults_for_timestamp
    if (value === null || value === undefined) {
      return;
    }

    if ((this.type === 'json' || this.type === 'jsonb') && isObject(value)) {
      // Default value for json will work only it is an expression
      return "default ('".concat(JSON.stringify(value), "')");
    }

    var defaultVal = ColumnCompiler_MySQL.super_.prototype.defaultTo.apply(this, arguments);

    if (this.type !== 'blob' && this.type.indexOf('text') === -1) {
      return defaultVal;
    }

    return '';
  },
  unsigned: function unsigned() {
    return 'unsigned';
  },
  comment: function comment(_comment) {
    if (_comment && _comment.length > 255) {
      this.client.logger.warn('Your comment is longer than the max comment length for MySQL');
    }

    return _comment && "comment '".concat(_comment, "'");
  },
  first: function first() {
    return 'first';
  },
  after: function after(column) {
    return "after ".concat(this.formatter.wrap(column));
  },
  collate: function collate(collation) {
    return collation && "collate '".concat(collation, "'");
  }
});
module.exports = ColumnCompiler_MySQL;