// PostgreSQL Column Compiler
// -------
var inherits = require('inherits');

var ColumnCompiler = require('../../../schema/columncompiler');

var _require = require('lodash'),
    isObject = _require.isObject;

function ColumnCompiler_PG() {
  ColumnCompiler.apply(this, arguments);
  this.modifiers = ['nullable', 'defaultTo', 'comment'];
}

inherits(ColumnCompiler_PG, ColumnCompiler);
Object.assign(ColumnCompiler_PG.prototype, {
  // Types
  // ------
  bigincrements: 'bigserial primary key',
  bigint: 'bigint',
  binary: 'bytea',
  bit: function bit(column) {
    return column.length !== false ? "bit(".concat(column.length, ")") : 'bit';
  },
  bool: 'boolean',
  // Create the column definition for an enum type.
  // Using method "2" here: http://stackoverflow.com/a/10984951/525714
  enu: function enu(allowed, options) {
    options = options || {};
    var values = options.useNative && options.existingType ? undefined : allowed.join("', '");

    if (options.useNative) {
      if (!options.existingType) {
        this.tableCompiler.unshiftQuery("create type \"".concat(options.enumName, "\" as enum ('").concat(values, "')"));
      }

      return "\"".concat(options.enumName, "\"");
    }

    return "text check (".concat(this.formatter.wrap(this.args[0]), " in ('").concat(values, "'))");
  },
  double: 'double precision',
  decimal: function decimal(precision, scale) {
    if (precision === null) return 'decimal';
    return "decimal(".concat(this._num(precision, 8), ", ").concat(this._num(scale, 2), ")");
  },
  floating: 'real',
  increments: 'serial primary key',
  json: function json(jsonb) {
    if (jsonb) this.client.logger.deprecate('json(true)', 'jsonb()');
    return jsonColumn(this.client, jsonb);
  },
  jsonb: function jsonb() {
    return jsonColumn(this.client, true);
  },
  smallint: 'smallint',
  tinyint: 'smallint',
  datetime: function datetime() {
    var withoutTz = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : false;
    var precision = arguments.length > 1 ? arguments[1] : undefined;
    var useTz;

    if (isObject(withoutTz)) {
      useTz = withoutTz.useTz;
      precision = withoutTz.precision;
    } else {
      useTz = !withoutTz;
    }

    return "".concat(useTz ? 'timestamptz' : 'timestamp').concat(precision ? '(' + precision + ')' : '');
  },
  timestamp: function timestamp() {
    var withoutTz = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : false;
    var precision = arguments.length > 1 ? arguments[1] : undefined;
    var useTz;

    if (isObject(withoutTz)) {
      useTz = withoutTz.useTz;
      precision = withoutTz.precision;
    } else {
      useTz = !withoutTz;
    }

    return "".concat(useTz ? 'timestamptz' : 'timestamp').concat(precision ? '(' + precision + ')' : '');
  },
  uuid: 'uuid',
  // Modifiers:
  // ------
  comment: function comment(_comment) {
    var columnName = this.args[0] || this.defaults('columnName');
    this.pushAdditional(function () {
      this.pushQuery("comment on column ".concat(this.tableCompiler.tableName(), ".") + this.formatter.wrap(columnName) + ' is ' + (_comment ? "'".concat(_comment, "'") : 'NULL'));
    }, _comment);
  }
});

function jsonColumn(client, jsonb) {
  if (!client.version || parseFloat(client.version) >= 9.2) return jsonb ? 'jsonb' : 'json';
  return 'text';
}

module.exports = ColumnCompiler_PG;