// SQLite3: Column Builder & Compiler
// -------
var inherits = require('inherits');

var SchemaCompiler = require('../../../schema/compiler');

var _require = require('lodash'),
    some = _require.some; // Schema Compiler
// -------


function SchemaCompiler_SQLite3() {
  SchemaCompiler.apply(this, arguments);
}

inherits(SchemaCompiler_SQLite3, SchemaCompiler); // Compile the query to determine if a table exists.

SchemaCompiler_SQLite3.prototype.hasTable = function (tableName) {
  var sql = "select * from sqlite_master " + "where type = 'table' and name = ".concat(this.formatter.parameter(tableName));
  this.pushQuery({
    sql: sql,
    output: function output(resp) {
      return resp.length > 0;
    }
  });
}; // Compile the query to determine if a column exists.


SchemaCompiler_SQLite3.prototype.hasColumn = function (tableName, column) {
  this.pushQuery({
    sql: "PRAGMA table_info(".concat(this.formatter.wrap(tableName), ")"),
    output: function output(resp) {
      var _this = this;

      return some(resp, function (col) {
        return _this.client.wrapIdentifier(col.name) === _this.client.wrapIdentifier(column);
      });
    }
  });
}; // Compile a rename table command.


SchemaCompiler_SQLite3.prototype.renameTable = function (from, to) {
  this.pushQuery("alter table ".concat(this.formatter.wrap(from), " rename to ").concat(this.formatter.wrap(to)));
};

module.exports = SchemaCompiler_SQLite3;