/* eslint max-len: 0 */
// PostgreSQL Table Builder & Compiler
// -------
var inherits = require('inherits');

var TableCompiler = require('../../../schema/tablecompiler');

var _require = require('lodash'),
    has = _require.has;

function TableCompiler_PG() {
  TableCompiler.apply(this, arguments);
}

inherits(TableCompiler_PG, TableCompiler); // Compile a rename column command.

TableCompiler_PG.prototype.renameColumn = function (from, to) {
  return this.pushQuery({
    sql: "alter table ".concat(this.tableName(), " rename ").concat(this.formatter.wrap(from), " to ").concat(this.formatter.wrap(to))
  });
};

TableCompiler_PG.prototype.compileAdd = function (builder) {
  var table = this.formatter.wrap(builder);
  var columns = this.prefixArray('add column', this.getColumns(builder));
  return this.pushQuery({
    sql: "alter table ".concat(table, " ").concat(columns.join(', '))
  });
}; // Adds the "create" query to the query sequence.


TableCompiler_PG.prototype.createQuery = function (columns, ifNot) {
  var createStatement = ifNot ? 'create table if not exists ' : 'create table ';
  var sql = createStatement + this.tableName() + ' (' + columns.sql.join(', ') + ')';
  if (this.single.inherits) sql += " inherits (".concat(this.formatter.wrap(this.single.inherits), ")");
  this.pushQuery({
    sql: sql,
    bindings: columns.bindings
  });
  var hasComment = has(this.single, 'comment');
  if (hasComment) this.comment(this.single.comment);
};

TableCompiler_PG.prototype.addColumns = function (columns, prefix, colCompilers) {
  if (prefix === this.alterColumnsPrefix) {
    // alter columns
    var _iteratorNormalCompletion = true;
    var _didIteratorError = false;
    var _iteratorError = undefined;

    try {
      for (var _iterator = colCompilers[Symbol.iterator](), _step; !(_iteratorNormalCompletion = (_step = _iterator.next()).done); _iteratorNormalCompletion = true) {
        var col = _step.value;
        var quotedTableName = this.tableName();
        var type = col.getColumnType(); // We'd prefer to call this.formatter.wrapAsIdentifier here instead, however the context passed to
        // `this` instance is not that of the column, but of the table. Thus, we unfortunately have to call
        // `wrapIdentifier` here as well (it is already called once on the initial column operation) to give
        // our `alter` operation the correct `queryContext`. Refer to issue #2606 and PR #2612.

        var colName = this.client.wrapIdentifier(col.getColumnName(), col.columnBuilder.queryContext());
        this.pushQuery({
          sql: "alter table ".concat(quotedTableName, " alter column ").concat(colName, " drop default"),
          bindings: []
        });
        this.pushQuery({
          sql: "alter table ".concat(quotedTableName, " alter column ").concat(colName, " drop not null"),
          bindings: []
        });
        this.pushQuery({
          sql: "alter table ".concat(quotedTableName, " alter column ").concat(colName, " type ").concat(type, " using (").concat(colName, "::").concat(type, ")"),
          bindings: []
        });
        var defaultTo = col.modified['defaultTo'];

        if (defaultTo) {
          var modifier = col.defaultTo.apply(col, defaultTo);
          this.pushQuery({
            sql: "alter table ".concat(quotedTableName, " alter column ").concat(colName, " set ").concat(modifier),
            bindings: []
          });
        }

        var nullable = col.modified['nullable'];

        if (nullable && nullable[0] === false) {
          this.pushQuery({
            sql: "alter table ".concat(quotedTableName, " alter column ").concat(colName, " set not null"),
            bindings: []
          });
        }
      }
    } catch (err) {
      _didIteratorError = true;
      _iteratorError = err;
    } finally {
      try {
        if (!_iteratorNormalCompletion && _iterator.return != null) {
          _iterator.return();
        }
      } finally {
        if (_didIteratorError) {
          throw _iteratorError;
        }
      }
    }
  } else {
    // base class implementation for normal add
    TableCompiler.prototype.addColumns.call(this, columns, prefix);
  }
}; // Compiles the comment on the table.


TableCompiler_PG.prototype.comment = function (comment) {
  this.pushQuery("comment on table ".concat(this.tableName(), " is '").concat(this.single.comment, "'"));
}; // Indexes:
// -------


TableCompiler_PG.prototype.primary = function (columns, constraintName) {
  constraintName = constraintName ? this.formatter.wrap(constraintName) : this.formatter.wrap("".concat(this.tableNameRaw, "_pkey"));
  this.pushQuery("alter table ".concat(this.tableName(), " add constraint ").concat(constraintName, " primary key (").concat(this.formatter.columnize(columns), ")"));
};

TableCompiler_PG.prototype.unique = function (columns, indexName) {
  indexName = indexName ? this.formatter.wrap(indexName) : this._indexCommand('unique', this.tableNameRaw, columns);
  this.pushQuery("alter table ".concat(this.tableName(), " add constraint ").concat(indexName) + ' unique (' + this.formatter.columnize(columns) + ')');
};

TableCompiler_PG.prototype.index = function (columns, indexName, indexType) {
  indexName = indexName ? this.formatter.wrap(indexName) : this._indexCommand('index', this.tableNameRaw, columns);
  this.pushQuery("create index ".concat(indexName, " on ").concat(this.tableName()).concat(indexType && " using ".concat(indexType) || '') + ' (' + this.formatter.columnize(columns) + ')');
};

TableCompiler_PG.prototype.dropPrimary = function (constraintName) {
  constraintName = constraintName ? this.formatter.wrap(constraintName) : this.formatter.wrap(this.tableNameRaw + '_pkey');
  this.pushQuery("alter table ".concat(this.tableName(), " drop constraint ").concat(constraintName));
};

TableCompiler_PG.prototype.dropIndex = function (columns, indexName) {
  indexName = indexName ? this.formatter.wrap(indexName) : this._indexCommand('index', this.tableNameRaw, columns);
  indexName = this.schemaNameRaw ? "".concat(this.formatter.wrap(this.schemaNameRaw), ".").concat(indexName) : indexName;
  this.pushQuery("drop index ".concat(indexName));
};

TableCompiler_PG.prototype.dropUnique = function (columns, indexName) {
  indexName = indexName ? this.formatter.wrap(indexName) : this._indexCommand('unique', this.tableNameRaw, columns);
  this.pushQuery("alter table ".concat(this.tableName(), " drop constraint ").concat(indexName));
};

TableCompiler_PG.prototype.dropForeign = function (columns, indexName) {
  indexName = indexName ? this.formatter.wrap(indexName) : this._indexCommand('foreign', this.tableNameRaw, columns);
  this.pushQuery("alter table ".concat(this.tableName(), " drop constraint ").concat(indexName));
};

module.exports = TableCompiler_PG;